.option {
  background-color: #b5dcc0b8;
  padding: 15px;
  font-size: 1.1em;
  margin: 5px;
  width: 70px;
  height: 35px;
  border-radius: 15px;
  align-items: center;
  text-align: center;
  display: flex;
  justify-content: center;
  font-family: "GowunDodum";
}

.option-select {
  background-color: #8fcca0;
  padding: 15px;
  font-size: 1.3em;
  margin: 5px;
  width: 70px;
  height: 35px;
  border-radius: 15px;
  align-items: center;
  text-align: center;
  display: flex;
  justify-content: center;
  letter-spacing: -0.1em;
}

.ul {
  flex-flow: row wrap;
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  padding: 0;
  margin: 3px;
}
